.banner{
  margin-bottom: 1.8rem;
}
.banner-box{
  position: relative;
  height: 640px;
}
.banner-item{
  background-size: cover;
  background-position: center;
  height: 640px;
}
.banner-1{
  background-image: url('../images/banner-1.png');
}
.banner-2{
  background-image: url('../images/banner-2.png');
}
.banner-3{
  background-image: url('../images/banner-3.png');
}
// section-banner
.section-banner{
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  width: 1116px;
  height: 120px;
  background-color: rgba($color: #100229, $alpha: .8);
  .swiper-container{
    height: 120px;
  }
}
.swiper-container2{
  width: 100%;
  height: 120px;
  overflow: hidden;
  padding: 1rem 0;
  .swiper-wrapper{
    min-width: 1116px;
  }
  .coin-info-box{
    width: 100%;
    height: 100%;
    color: rgba($color: #d8d4f5, $alpha: .85);
    padding: 0 .5rem;
    border-right: 1px solid rgba($color: $white, $alpha: .3);
    p{
      font-weight: 200;
      margin-bottom: 0;
    }
    small{
      font-weight: 200;
    }
  
  }
  .button{
    position: absolute;
    background-color: $black;
    width: 42px;
    height: 120px;
    color: $white;
    &.swiper-button-next{
      right: -42px;
      top: 0;
    }
    &.swiper-button-prev{
      left: -42px;
      top: 0;
    }
  }
}