.header{
  display: flex;
  align-items: center;
}
.header{
  .btn-linearY{
    padding: .28rem .625rem;
    font-weight: 300;
  }
  .btn-outline-wa{
    border: 1px solid rgba($white,.2);
    color: $white;
    padding: .23em .625rem;
    font-weight: 300;
    &#dropdown-lang{
      // min-width: 5.6rem;
      text-align: right;
    }
  }
}


.kw-close{
  display: block;
  width: 50px;
  height: 40px;
  padding: 13px;
  margin: 2px;
  position: absolute;
  top: 0;
  left: 0;
}


.mm-wrapper_opening .menu[href="#menu"]{
  display: none;
}
.kw-close[href="#page"]{
  display: none;
}
.mm-wrapper_opening .kw-close[href="#page"]{
  display: block;
}

#dropdown-coin.dropdown-toggle::after{
  display: none;
}
.title span{
  opacity: .5;
  font-size: .75rem;
}

.footer{
  position: absolute;
  width: 100%;
  padding: 1.25rem 1rem;
  font-weight: 300;
  text-align: center;
  background-color: $primary;
  color: rgba($white,.7);
  bottom: 0;
}

.login-user{
  display: flex;
  align-items: center;
  width: 100%;
}