.btn-linearY{
  background-image: linear-gradient(270deg, #FAD961 0%, #F76B1C 100%);
  color: $white;
  border-color: rgba($color: #000000, $alpha: 0);
  border: none;
  &:hover{
    color:$white;
  }
}
.btn-linearB{
  background-image:  linear-gradient(269deg, #30BBE7 1%, #0D54B4 100%);
  color: $white;
  border-color: rgba($color: #000000, $alpha: 0);
  border: none;
  &:hover{
    color:$white;
  }
}
a.back{
  display: inline-block;
  padding-top: 1rem;
  padding-bottom: 1rem;
  color: $black;
}